/* ========================= */
/* FORGOT PASSWORD PAGE */
/* ========================= */

.forgot-password-page {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, #f8fafc 0%, #e0e7ff 50%, #fae8ff 100%);
    padding: 60px 20px;
    position: relative;
    overflow: hidden;
}

/* Animated Background Orbs */
.orb {
    position: absolute;
    border-radius: 50%;
    animation: float 8s ease-in-out infinite;
    pointer-events: none;
}

.orb-1 {
    width: 500px;
    height: 500px;
    background: radial-gradient(circle, rgba(79, 70, 229, 0.15), transparent);
    top: -250px;
    right: -100px;
    animation-delay: 0s;
}

.orb-2 {
    width: 400px;
    height: 400px;
    background: radial-gradient(circle, rgba(124, 58, 237, 0.12), transparent);
    bottom: -150px;
    left: -100px;
    animation-delay: 2s;
    animation-direction: reverse;
}

@keyframes float {

    0%,
    100% {
        transform: translateY(0) scale(1);
    }

    50% {
        transform: translateY(-20px) scale(1.05);
    }
}

/* ========================= */
/* CONTAINER */
/* ========================= */

.forgot-password-container {
    width: 100%;
    max-width: 440px;
    position: relative;
    z-index: 1;
    animation: fadeInUp 0.6s ease-out;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* ========================= */
/* BRAND SECTION */
/* ========================= */

.brand-section {
    text-align: center;
    margin-bottom: 32px;
    animation: slideDown 0.6s ease-out;
}

@keyframes slideDown {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.brand-logo {
    font-size: 56px;
    margin-bottom: 12px;
    filter: drop-shadow(0 4px 12px rgba(0, 0, 0, 0.15));
    animation: bounce 2s ease-in-out infinite;
}

@keyframes bounce {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-8px);
    }
}

.brand-name {
    font-size: 28px;
    font-weight: 800;
    color: #1e293b;
    margin: 0 0 4px 0;
    letter-spacing: -0.02em;
    background: linear-gradient(135deg, #1e293b 0%, #4f46e5 100%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.brand-tagline {
    font-size: 12px;
    font-weight: 600;
    color: #64748b;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 0;
}

/* ========================= */
/* MAIN CARD */
/* ========================= */

.forgot-password-card {
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 20px;
    padding: 40px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(255, 255, 255, 0.5);
    animation: scaleIn 0.6s ease-out 0.2s backwards;
}

@keyframes scaleIn {
    from {
        opacity: 0;
        transform: scale(0.95);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

/* ========================= */
/* CARD HEADER */
/* ========================= */

.card-header {
    text-align: center;
    margin-bottom: 32px;
}

.card-header h1 {
    font-size: 28px;
    font-weight: 800;
    color: #1e293b;
    margin: 0 0 12px 0;
    letter-spacing: -0.02em;
}

.subtitle {
    font-size: 14px;
    font-weight: 500;
    color: #64748b;
    line-height: 1.6;
    margin: 0;
}

/* ========================= */
/* FORM */
/* ========================= */

.form-group {
    margin-bottom: 24px;
}

.form-group label {
    display: flex;
    align-items: center;
    gap: 6px;
    font-size: 14px;
    font-weight: 600;
    color: #475569;
    margin-bottom: 10px;
    letter-spacing: 0.01em;
}

.form-group label svg {
    color: #4f46e5;
}

.form-group input {
    width: 100%;
    padding: 14px 16px;
    border: 1.5px solid rgba(203, 213, 225, 0.5);
    border-radius: 10px;
    font-size: 15px;
    font-weight: 500;
    color: #1e293b;
    background: white;
    transition: all 0.3s ease;
    font-family: inherit;
}

.form-group input:focus {
    outline: none;
    border-color: #4f46e5;
    box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
    background: #fafbfc;
}

.form-group input::placeholder {
    color: #94a3b8;
    font-weight: 400;
}

.form-group input:disabled {
    background: #f1f5f9;
    cursor: not-allowed;
}

.form-group small {
    display: block;
    margin-top: 8px;
    font-size: 12px;
    font-weight: 500;
    color: #64748b;
}

/* ========================= */
/* ERROR MESSAGE */
/* ========================= */

.error-message {
    padding: 12px 16px;
    background: linear-gradient(135deg, #fee2e2, #fecaca);
    border-left: 4px solid #ef4444;
    border-radius: 8px;
    color: #991b1b;
    font-size: 13px;
    font-weight: 600;
    margin-bottom: 20px;
    animation: shake 0.4s ease-in-out;
}

@keyframes shake {

    0%,
    100% {
        transform: translateX(0);
    }

    25% {
        transform: translateX(-8px);
    }

    75% {
        transform: translateX(8px);
    }
}

/* ========================= */
/* BUTTONS */
/* ========================= */

.reset-button {
    width: 100%;
    padding: 14px;
    background: linear-gradient(135deg, #4f46e5 0%, #7c3aed 100%);
    color: white;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    font-weight: 700;
    cursor: pointer;
    box-shadow: 0 6px 20px rgba(79, 70, 229, 0.3);
    transition: all 0.3s ease;
    margin-bottom: 20px;
}

.reset-button:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 8px 28px rgba(79, 70, 229, 0.4);
}

.reset-button:active:not(:disabled) {
    transform: translateY(0);
}

.reset-button:disabled {
    opacity: 0.7;
    cursor: not-allowed;
}

/* ========================= */
/* CARD FOOTER */
/* ========================= */

.card-footer {
    text-align: center;
    padding-top: 20px;
    border-top: 1px solid rgba(203, 213, 225, 0.3);
}

.back-link {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    color: #4f46e5;
    text-decoration: none;
    font-size: 14px;
    font-weight: 600;
    transition: all 0.3s ease;
}

.back-link:hover {
    color: #7c3aed;
    transform: translateX(-4px);
}

/* ========================= */
/* SIGNUP PROMPT */
/* ========================= */

.signup-prompt {
    text-align: center;
    margin-top: 24px;
    font-size: 14px;
    font-weight: 500;
    color: #64748b;
}

.signup-prompt a {
    color: #4f46e5;
    font-weight: 700;
    text-decoration: none;
    transition: all 0.3s ease;
}

.signup-prompt a:hover {
    color: #7c3aed;
    text-decoration: underline;
}

/* ========================= */
/* SUCCESS CARD */
/* ========================= */

.success-card {
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 20px;
    padding: 48px 40px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(255, 255, 255, 0.5);
    text-align: center;
    animation: scaleIn 0.6s ease-out;
}

.success-icon {
    color: #10b981;
    margin-bottom: 24px;
    animation: successPop 0.6s ease-out 0.3s backwards;
}

@keyframes successPop {
    0% {
        opacity: 0;
        transform: scale(0);
    }

    50% {
        transform: scale(1.1);
    }

    100% {
        opacity: 1;
        transform: scale(1);
    }
}

.success-card h1 {
    font-size: 28px;
    font-weight: 800;
    color: #1e293b;
    margin: 0 0 16px 0;
}

.success-message {
    font-size: 15px;
    font-weight: 500;
    color: #475569;
    line-height: 1.7;
    margin: 0 0 32px 0;
}

.success-message strong {
    color: #4f46e5;
    font-weight: 700;
}

/* ========================= */
/* SUCCESS INFO */
/* ========================= */

.success-info {
    background: linear-gradient(135deg, #f0f9ff, #e0f2fe);
    border-left: 4px solid #3b82f6;
    border-radius: 12px;
    padding: 20px;
    margin-bottom: 24px;
    text-align: left;
}

.success-info p {
    font-size: 14px;
    font-weight: 700;
    color: #1e40af;
    margin: 0 0 12px 0;
}

.success-info ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.success-info li {
    font-size: 13px;
    font-weight: 500;
    color: #475569;
    padding-left: 24px;
    margin-bottom: 8px;
    position: relative;
    line-height: 1.5;
}

.success-info li:last-child {
    margin-bottom: 0;
}

.success-info li::before {
    content: "";
    position: absolute;
    left: 8px;
    color: #3b82f6;
    font-weight: 700;
}

/* ========================= */
/* RESEND BUTTON */
/* ========================= */

.resend-button {
    width: 100%;
    padding: 14px;
    background: white;
    color: #4f46e5;
    border: 1.5px solid rgba(79, 70, 229, 0.3);
    border-radius: 10px;
    font-size: 15px;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-bottom: 16px;
}

.resend-button:hover {
    background: #f8fafc;
    border-color: #4f46e5;
    transform: translateY(-1px);
}

.back-to-login {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    color: #4f46e5;
    text-decoration: none;
    font-size: 14px;
    font-weight: 600;
    transition: all 0.3s ease;
}

.back-to-login:hover {
    color: #7c3aed;
    transform: translateX(-4px);
}

/* ========================= */
/* RESPONSIVE DESIGN */
/* ========================= */

@media (max-width: 480px) {
    .forgot-password-page {
        padding: 40px 16px;
    }

    .forgot-password-card,
    .success-card {
        padding: 32px 24px;
    }

    .brand-logo {
        font-size: 48px;
    }

    .brand-name {
        font-size: 28px;
    }

    .card-header h1,
    .success-card h1 {
        font-size: 24px;
    }

    .orb-1,
    .orb-2 {
        width: 300px;
        height: 300px;
    }
}